//引入mockjs
const Mock=require('mockjs')
//获取mock.Random对象
const Random=Mock.Random;
//使用mockjs模拟数据
//轮播图
let data=[]
for(let i=0;i<4;i++){
    let template={
        'Image':Random.image('740x323','#894fc4','','png','banner')
    }
    data.push(template)
}
Mock.mock('/data/getImage','get',data)
//首页推荐内容
let data1=[]
for(let i=0;i<20;i++){
    let template={
        'Image':Random.image('280x210','#e74c3c','','png',''),
        'Title':Random.title(3,5),
        'Content':Random.csentence(3, 5),
        'Icon':Random.image('20x20','#e74c3c','','png',''),
        'Name':Random.cname()
    }
    data1.push(template)

}
Mock.mock('/data/getIndex','get',data1)
//首页推荐文章
let data2=[]
for(let i=0;i<6;i++){
    let template={
        'Image':Random.image('160x120','#006d7c','','png',''),
        'Title':Random.title(3,5),
        'Content':Random.csentence(30),
        'Icon':Random.image('20x20','#e74c3c','','png',''),
        'Name':Random.cname(),
        'Number':Mock.mock({
            "number|1-100":10
        }),
        'Date':Random.date(),
        'Time':Mock.mock('@time("H:m")')

    }
    data2.push(template)
}
Mock.mock('/data/getArticle','get',data2)
//首页侧边栏广告
let data3=[]
for(let i=0;i<3;i++){
    let template={
        'Image':Random.image('280x125','#e90106','','png','招聘'),
    }
    data3.push(template)
}
Mock.mock('/data/hot','get',data3)
//首页排行榜
let data4=[]
for(let i=0;i<6;i++){
    let template={
        'Image':Random.image('60x60','#895b39','','png',''),
        'Name':Random.cname(3),
        'Number':Mock.mock({
            "number|1-10":1
        })
    }
    data4.push(template)
}
Mock.mock('/data/rank','get',data4)
//首页推荐团队
let data5=[]
for(let i=0;i<2;i++){
    let template={
        'Image':Random.image('100x75','#6177da','','png',''),
        'Number':Mock.mock({
            "number|10-100":10,
        }),
        'Name':Random.cname(1),
        'Address':Mock.mock('@province()')
    }
    data5.push(template)
}
Mock.mock('/data/team','get',data5)
//合作伙伴
let data6=[]
for(let i=0;i<16;i++){
    var template={
    'Image':Random.image('150x50','#e60006','','png',''),
    'Word':Mock.mock('@word(5)')
}
    data6.push(template)
}
Mock.mock('/data/partner','get',data6)


