<template>
    <div>
        <head-view></head-view>
   <el-carousel :interval="4000" type="card" height="323px">
        <el-carousel-item v-for="item in list" :key="item.index">
            <img :src="item.Image" >
        </el-carousel-item>
  </el-carousel>
   <div class="wpn">
       <div class="pos">
        <template>
            <el-tabs class="pos-tabs">
                <el-tab-pane label="首页推荐" name="first">首页推荐</el-tab-pane>
                <el-tab-pane label="佳作分享" name="second">佳作分享</el-tab-pane>
                <el-tab-pane label="最新作品" name="third">最新作品</el-tab-pane>
            </el-tabs>
        </template>
           <ul class="h-soup">
              <li>
                <a href="#" class="txt">解决问题，你想各种办法，解决一个问题，然后接着解决下一个问题，循环往复，等你解决了足够多的问题，你就可以回家了~ </a>
              </li>
           </ul>
       </div>
    <ul class="post post-works mtw">
        <router-link to="/detail"><li v-for="item in list1" :key="item.index">
            <div class="shade" style="opacity:0px">
            </div>
            <div class="cover">
                <a href="#">
                    <img :src="item.Image" style="width: 280px;height: 210px;">
                </a>
            </div>
            <div class="info">
                <h4 class="title ellipsis download">{{item.Title}}</h4>
                <div class="msg mtn">
                    <p>{{item.Content}}</p>
                </div>
                <p class="user">
                    <a href="#">
                        <img :src="item.Icon" alt="">
                        <strong class="name">
                            <em>{{item.Name}}</em>
                        </strong>
                    </a>
                </p>
            </div>
        </li>
        </router-link>
   </ul>
   <div class="h_page mtn mbw">
      <div class="block">
         <el-pagination layout="prev, pager, next" :total="1000" background></el-pagination>
      </div>
   </div>
</div>
<div class="bg-white">
    <div class="wpn">
         <div class="w820 z">
            <template>
            <el-tabs class="pos-tabs">
                <el-tab-pane label="推荐文章" name="first">推荐文章</el-tab-pane>
                <el-tab-pane label="最新文章" name="second">最新文章</el-tab-pane>
            </el-tabs>
        </template>
            <div class="h-article-box">
                <ul class="h-article-list">
                    <li v-for="item in list2" :key="item.index">
                        <a href="#">
                           <span class="cover">
                               <img :src="item.Image" style="width: 160px;height: 120px ;">
                           </span>
                           <div class="h-article-info">
                               <h1>
                                   <span class="tag bg-blue">经验观点</span>
                                   <span>{{item.Title}}</span>
                                   <div class="data">
                                       <i class="iconfont">&#xe628;</i>
                                         {{item.Date}} {{item.Time}}
                                   </div>
                               </h1>
                               <p>{{item.Content}}</p>
                               <div class="mtm">
                                    <span class="user">
                                            <img :src="item.Icon" alt="">
                                            <strong class="name">
                                            {{item.Name}}
                                            </strong>   
                                    </span>
                                    <span>
                                        <i class="iconfont icon-eye">&#xe7b4;</i>
                                        <em>{{item.Number.number}}</em>
                                    </span>
                                    <span>
                                        <i class="iconfont icon-comment">&#xe623;</i>
                                        <em>{{item.Number.number}}</em>
                                    </span>
                                    <span>
                                        <i class="iconfont icon-leaf">&#xe63c;</i>
                                        <em>{{item.Number.number}}</em>
                                    </span>
                                </div>
                           </div>
                        </a>
                    </li>
                </ul>
                <div class="h_page mtn mbw">
                    <div class="block">
                        <el-pagination layout="prev, pager, next" :total="1000" background class="block-el"></el-pagination>
                    </div>  
                </div>
            </div>
         </div>
         <div class="w280 y mtv">
             <h1 class="h-tit-aside">热招职位</h1>
             <ul class="h-aside-list">
                 <li v-for="item in list3" :key="item.index">
                     <a href="#">
                         <img class="cover" :src="item.Image" style="width: 280px;height: 125;">
                     </a>
                 </li>
             </ul>
             <a href="#" class="more">更多</a>
             <h1 class="h-tit-aside mtt">热门课程</h1>
             <ul class="h-aside-list">
                 <li v-for="item in list3" :key="item.index">
                     <a href="#">
                         <img class="cover" :src="item.Image" style="width: 280px;height: 125;">
                     </a>
                 </li>
             </ul>
             <a href="#" class="more">更多</a>
         </div>
    </div>
</div>
        <div class="topGrow">
            <div class="down">
                <div class="z top">
                    <template>
            <el-tabs>
                <el-tab-pane label="人气榜" name="first">人气榜</el-tab-pane>
                <el-tab-pane label="成长榜" name="second">成长榜</el-tab-pane>
            </el-tabs>
        </template>
                    <ul class="con">
                        <li class="z" v-for="item in list4" :key="item.index">
                            <div class="showWarp">
                                <p class="z pos">
                                    <a href="#">
                                        <img :src="item.Image" style="width: 60px;height: 60px;" class="headImg">
                                    </a>
                                    <img src="../../public/img/hat1.png" style="width: 51px;height: 45px;" class="hat1">
                                </p>
                                <div class="z">
                                    <a href="#">
                                        <h3>{{item.Name}}</h3>
                                    </a>
                                    <div class="floor">
                                        <img src="../../public/img/square.png" alt="">
                                        <span>{{item.Number.number}}</span>
                                        /
                                        <span>{{item.Number.number}}</span>
                                        /
                                        <span>{{item.Number.number}}</span>
                                    </div>
                                    <div class="achieve">
                                        <img src="../../public/img/hat1.png" style="width: 20px;height: 20px;">
                                        <img src="../../public/img/hat1.png" style="width: 20px;height: 20px;">
                                        <img src="../../public/img/hat1.png" style="width: 20px;height: 20px;">
                                        <img src="../../public/img/hat1.png" style="width: 20px;height: 20px;">
                                    </div>
                                </div>
                            </div>
                        </li>
                    </ul>
                </div>
                <div class="z team">
                    <div class="c1">
                        <h3 class="z">推荐团队</h3>
                       <div class="block">
                             <el-pagination layout="prev, pager, next" :total="50" background class="block-team"></el-pagination>
                        </div>
                    </div>
                    <ul>
                        <li class="teamList pos" v-for="item in list5" :key="item.index">
                            <div class="pos1">
                                <a href="#" class="z">
                                    <img :src="item.Image" style="width: 100px;height: 75px;">
                                </a>
                                <div class="z">
                                    <p class="team-name">
                                        <a href="#">
                                            <span class="z">{{item.Name}}</span>
                                            <span class="z">{{item.Address}}</span>
                                        </a>
                                    </p>
                                    <div class="floor">
                                        <img src="../../public/img/square.png" alt="">
                                        <span>{{item.Number.number}}</span>
                                        /
                                        <span>{{item.Number.number}}</span>
                                        /
                                        <span>{{item.Number.number}}</span>
                                    </div>
                                    <p class="part_3">
                                        <img src="../../public/img/agree.png" alt="">
                                        <span>{{item.Number.number}}</span>
                                        <img src="../../public/img/heart.png" alt="">
                                        <span>{{item.Number.number}}</span>
                                        <img src="../../public/img/peo.png" alt="">
                                        <span>{{item.Number.number}}</span>
                                    </p>
                                </div>
                               
                            </div>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
            <div class="parter">
                <h2 class="h-tit2">合作伙伴</h2>
                <div class="h-partners">
                    <ul class="logo">
                        <li v-for="item in list6" :key="item.index">
                            <a href="#">
                                <img :src="item.Image" style="display: inline;">
                            </a>
                        </li>
                    </ul>
                    <ul class="text">
                        <li v-for="item in list6" :key="item.index">
                            <a href="#">{{item.Word}}</a>
                        </li>
                    </ul>
                </div>
            </div>
    </div>
</template>

<script>
export default {
    data(){
        return {
            list:[],
            list1:[],
            list2:[],
            list3:[],
            list4:[],
            list5:[],
            list6:[]
        }
    },
    methods:{
        getImage(){
            var url="/data/getImage";
            this.axios.get(url).then(result=>{
                this.list=result.data;
            })
             
        },
        getIndex(){
            var url="/data/getIndex";
            this.axios.get(url).then((data)=>{
                this.list1=data.data;
            })
        },
        getArticle(){
            var url="/data/getArticle";
            this.axios.get(url).then((data)=>{
                this.list2=data.data;
            })
        },
        hot(){
            var url="/data/hot";
            this.axios.get(url).then((data)=>{
                this.list3=data.data;
            })
        },
        rank(){
            var url='/data/rank';
            this.axios.get(url).then((data)=>{
                this.list4=data.data;
            })
        },
        team(){
            var url='/data/team';
            this.axios.get(url).then((data)=>{
                this.list5=data.data;
            })
        },
        partner(){
            var url="/data/partner";
            this.axios.get(url).then((data)=>{
                this.list6=data.data;
            })
        }
    },
    created(){
        this.getImage();
        this.getIndex();
        this.getArticle();
        this.hot();
        this.rank();
        this.team();
        this.partner();
    }
}
</script>

<style scoped>
    @import url('../../public/css/index.css');
     .el-carousel__item img {
         width: 100%;
         height: 100%;
  }
  .el-carousel,el-carousel--card{
      width: 1480px;
      margin: 0 auto;
  }
   .el-dropdown-link {
    cursor: pointer;
    color: #34495e;
  }
  .firstpage{
      color: #3498db;
  }
  .el-icon-arrow-down {
    font-size: 14px;
  }
  .el-dropdown{
      line-height: 70px;
      width: 80px;
      height: 57px;
      text-align: center;
  }
 .block-el{
     margin:25px 0 auto;
 }
 .block-team{
     margin: 0 88px;
 }
 .pos-tabs{
     padding-top: 20px;
 }
</style>



